import { defineStore } from "pinia";
import axios from "axios";
import { config } from "./config";

interface Task {
  id: number;
  title: string;
  url: string;
  status: string;
}

interface Category {
  id: number;
  title: string;
  tasks: Task[];
}

export const questsStore = defineStore("quests", {
  state: ()  => ({
    categories: [
      {
        id: 0,
        title: "Social",
        tasks: [
          {
            id: 0,
            title: "Task 1",
            url: "https://vk.com/al_feed.php",
            status: "VERIFYING"
          },
          {
            id: 1,
            title: "Tasks 2",
            url: "https://vk.com/al_feed.php",
            status: "CLAIMED"
          },
          {
            id: 2,
            title: "Task 1",
            url: "https://vk.com/al_feed.php",
            status: "COMPLETED"
          },
          {
            id: 3,
            title: "Tasks 2",
            url: "https://vk.com/al_feed.php",
            status: "NOT_STARTED"
          },
          {
            id: 4,
            title: "Tasks 3",
            url: "https://vk.com/al_feed.php",
            status: "NOT_STARTED"
          },
        ],
      },
      {
        id: 1,
        title: "Social 2",
        tasks: [
          {
            id: 5,
            title: "Task 1",
            url: "https://vk.com/al_feed.php",
            status: "VERIFYING"
          },
          {
            id: 6,
            title: "Tasks 2",
            url: "https://vk.com/al_feed.php",
            status: "CLAIMED"
          },
          {
            id: 7,
            title: "Task 1",
            url: "https://vk.com/al_feed.php",
            status: "COMPLETED"
          },
          {
            id: 8,
            title: "Tasks 2",
            url: "https://vk.com/al_feed.php",
            status: "NOT_STARTED"
          },
          {
            id: 9,
            title: "Tasks 3",
            url: "https://vk.com/al_feed.php",
            status: "NOT_STARTED"
          },
        ],
      },
    ] as Category[],
    promoTasks: [
      {
        title: "Quest Name 1",
        description:
          "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Duis fringilla, metus sit amet volutpat convallis, neque sem ullamcorper.",
      },
      {
        title: "Quest Name 2",
        description:
          "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Duis fringilla, metus sit amet volutpat convallis, neque sem ullamcorper.",
      },
      {
        title: "Quest Name 3",
        description:
          "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Duis fringilla, metus sit amet volutpat convallis, neque sem ullamcorper.",
      },
      {
        title: "Quest Name 4",
        description:
          "Lorem ipsum dolor sit amet, consectetur adipiscing elit. Duis fringilla, metus sit amet volutpat convallis, neque sem ullamcorper.",
      },
    ],
  }),

  actions: {
    fetchQuests() {
      return this.promoTasks;
    },

    acceptTask(task: any) {
      const category = this.categories.find((cat) =>
          cat.tasks.some((t) => t.id === task.id)
      );
      if (category) {
        const taskToUpdate = category.tasks.find((t) => t.id === task.id);
        if (taskToUpdate) {
          taskToUpdate.status = "VERIFYING";

          // TODO: заменить ниже на получение ответа от сервера
          setTimeout(() => {
            taskToUpdate.status = "COMPLETED";
          }, 5000);
        }
      }
    },

    claimReward(task: any) {
      const category = this.categories.find((cat) =>
          cat.tasks.some((t) => t.id === task.id)
      );
      if (category) {
        const taskToUpdate = category.tasks.find((t) => t.id === task.id);
        if (taskToUpdate) {
          taskToUpdate.status = "CLAIMED";
        }
      }
    },

    async joinQuest(quest: any) {
      const response = await axios.post(
        config.backendURL + "/orders/join-quest",
        {
          quest,
          withCredentials: true,
        }
      );

      if (response.status !== 201) {
        throw new Error(
          "Не удалось создать обращение. Неправильный статус ответа от сервера: " +
            response.status
        );
      }
    },
  },

  getters: {},
});
